import React, {useEffect} from "react"
import 'bootstrap/dist/css/bootstrap.min.css'
import { Container } from 'react-bootstrap';
import { Link } from "gatsby";
import { Helmet } from "react-helmet";
import { Image } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import { Navbar } from "react-bootstrap";
import { Collapse } from "react-bootstrap";
import { Row, Col, Button } from "react-bootstrap";

import Jason from "../../static/JasonPhoto.jpg"
import Belinda from "../../static/BelindaPhoto.jpg"
import Joseph from "../../static/JosephPhoto.jpg"
import Judy from "../../static/JudyPhoto.png"
import newsletter from "../../static/newsletter2(1).png"
import firstimg from "../../static/Mrs. Rajamani Teacher Photo 2019-2020.png"
import secondimg from "../../static/2020 Portrait.jpg"
import DDTVimg from "../../static/index.jpg"

var latestLink = "https://www.canva.com/design/DAEB5NGHJ5Q/f8YFMnbBYUdyZROJJxH4Ng/view?utm_content=DAEB5NGHJ5Q&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var firstLink = "https://www.canva.com/design/DAEDHBfSKfc/TZ7XXdSnt8WFeM0SMWKkcQ/view?utm_content=DAEDHBfSKfc&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var DDTVint = "https://www.canva.com/design/DAEDY1sY2HY/24LpLTpkIRhKnRb-xjgIZg/view?utm_content=DAEDY1sY2HY&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var eventLink = "https://www.canva.com/design/DAEDY1sY2HY/24LpLTpkIRhKnRb-xjgIZg/view?utm_content=DAEDY1sY2HY&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var newsTitle = "Learning From Afar"
var newsDesc = "A dive into the impact of distance learning on the education system"


export default function Home() {
  useEffect(() => {
    window.onscroll = function() {
      var navlinks = document.getElementsByClassName('navlink')
      if(window.pageYOffset >= 75) {
        document.getElementById('navbar').style.backgroundColor = "rgba(40,50,70,255)";
        for (var i=0;i<navlinks.length;i++) {
          navlinks[i].classList.add("thisHover")
          navlinks[i].classList.remove("hovering")
        }
      }
      else {
        document.getElementById('navbar').style.backgroundColor = "rgba(0.3,0.3,0.3,0.5)";
        for (var p=0;p<navlinks.length;p++) {
          navlinks[p].classList.add("hovering")
          navlinks[p].classList.remove("thisHover")
        }
      }
    };
  }, []);

  function openLink(e) {
    window.open(e)
  }

  return (
  <Container fluid>
    <Helmet>
        <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, minimum-scale=1.0, 
        height=device-height, target-densitydpi=device-dpi, shrink-to-fit=no, user-scalable=no" />
        <title>Meet The Team - BAYouth</title>
        <body id="events-page" />
    </Helmet>
    <Navbar collapseOnSelect expand="lg" fixed="top" id="navbar" style={{ backgroundColor: `rgba(0.3,0.3,0.3,0.5)` }}>
        <Navbar.Brand href="/" style={{ paddingRight: `1.5rem`, color: `white` }}>BAYouth</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
                <Nav.Link href="/articles" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Our Articles</Nav.Link>
                <Nav.Link href="/events" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Events</Nav.Link>
            </Nav>
            <Nav>
                <Nav.Link href="/meetus" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Meet the Team</Nav.Link>
            </Nav>
        </Navbar.Collapse>
    </Navbar>

    <div id="meet-body">
      <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} style={{ padding: 0, margin: `3.5rem 0` }} className="center overlay">
          </Col>
      </Row>
    </div>

    <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Our Students</p>
        </Col>
      </Row>
    </div>

    <Row style={{ margin: `15px 0 0 0`, padding: 0 }}>
        <Col className="cardCol" xs={12} sm={12} md={6} lg={4} xl={4}>
            <aside class="profile-card">
            
            <header style={{ padding: `15px 0 5px 0` }}>

                <img src={Jason}></img>

                <h1>Jason Shan</h1>
                
                <h2>Sophomore</h2>
                
            </header>
            
            <div class="profile-bio">
                
                <p style={{ textAlign: `center`, marginBottom: `15px` }}>Jason Shan is a rising sophomore at Lynbrook High School. 
                    Outisde of school, he likes to participate in community service organizations, 
                    including SVCU, Cupertino Educational Endowment Foundation, and Foundation Student Organization. 
                    His pastimes include programming, reading fiction, and writing.</p>
                
            </div>
            </aside>
        </Col>
        <Col className="cardCol" xs={12} sm={12} md={6} lg={4} xl={4}>
            <aside class="profile-card">
            
            <header style={{ padding: `15px 0 5px 0` }}>

                <img src={Joseph}></img>

                <h1>Joseph Shan</h1>
                
                <h2>Sophomore</h2>
                
            </header>
            
            <div class="profile-bio">
                
                <p style={{ textAlign: `center`, marginBottom: `15px` }}>Joseph Shan is a rising sophomore at Lynbrook High School. 
                He lives in San Jose, California. In his downtime, he enjoys playing basketball and reading. He has 3 brothers, 
                one of whom is his twin, and a pet turtle.</p>
                
            </div>
            </aside>
        </Col>
        <Col className="cardCol" xs={12} sm={12} md={6} lg={4} xl={4}>
            <aside class="profile-card">
            
            <header style={{ padding: `15px 0 5px 0` }}>

                <img src={Judy}></img>

                <h1>Judy Zeng</h1>
                
                <h2>Junior</h2>
                
            </header>
            
            <div class="profile-bio">
                
                <p style={{ textAlign: `center`, marginBottom: `15px` }}>Judy Zeng, a rising junior at Kehillah High School, is an editor for the SVCU Youth Newsletter. 
                She dances as her main extracurricular, but she also likes to sing and play the piano. She also spends her time volunteering in SVCU, a nonprofit organization, 
                where she tutored kids in math and worked as a summer camp counselor.</p>
                
            </div>
            </aside>
        </Col>
        <Col className="cardCol" xs={12} sm={12} md={6} lg={4} xl={4}>
            <aside class="profile-card">
            
            <header style={{ padding: `15px 0 5px 0` }}>

                <img src={Belinda}></img>

                <h1>Belinda Chen</h1>
                
                <h2>Junior</h2>
                
            </header>
            
            <div class="profile-bio">
                
                <p style={{ textAlign: `center`, marginBottom: `15px` }}>Belinda Chen is a rising junior at Lynbrook High School, 
                as well as an editor for the SVCU Youth Committee Newsletter. She has been an acrobatic gymnast for five years, 
                and currently competes for ATA. In her free time, she enjoys baking snacks for her friends and watching Netflix. 
                She volunteers at non-profit organizations, such as SVCU and SYC, where she tutors younger kids in math, reading, 
                and writing, and helps organize events.</p>
                
            </div>
            </aside>
        </Col>
    </Row>

    <div className="headerBack" style={{ marginTop: 0 }}>
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Contact Us</p>
        </Col>
      </Row>
    </div>

    <Row className="bottom" id="tohere" >
      <Col className="d-flex align-items-center justify-content-center back" style={{ margin: 0, padding: 0 }}>
          <form autoComplete="off" className="form" action="https://formspree.io/mzbjlnbq" method="POST">
              <div style={{ paddingTop: `1em`, paddingBottom: `1em` }}>
                  <div className="wrapper">
                      <div className="title">
                          <h1>Contact Us</h1>
                      </div>
                      <div className="contact-form">
                          <div className="input-fields">
                              <input type="text" name="name" className="input" placeholder="Name" required/>
                              <input type="text" name="email" className="input" placeholder="Email Address" required/>
                              <input type="text" name="subject" id="formSubject" className="input" placeholder="Subject" required/>
                          </div>
                          <div className="msg">
                              <textarea type="text" name="message" id="formMessage" placeholder="Message" required></textarea>
                          <button type="submit" className="btn">send</button>
                          </div>
                      </div>
                  </div>
              </div>
        </form>
      </Col>
    </Row>

  </Container>
  )}